<template>
  <div class="manage">
    <div class="nav">
      <a-breadcrumb>
        <a-breadcrumb-item><router-link to="/admin/home">首页</router-link></a-breadcrumb-item>
        <a-breadcrumb-item>书籍管理</a-breadcrumb-item>
      </a-breadcrumb>
    </div>
    <div class="serch_field">
      <a-form-model
        layout="inline"
      >

        <a-form-model-item style="width: 100%;">
          <a-form-model-item>
            <a-input
              placeholder="请输入书籍名称、作者、编号..."
              v-model="params.keyword"
              style="width: 220px;"
            ></a-input>
          </a-form-model-item>
          <a-button type="primary" @click="search" style="margin-right: 12px;">搜索</a-button>
          <a-button @click="reset">重置</a-button>
        </a-form-model-item>

        <a-form-model-item style="width: 100%">
          <a-form-model-item label="书籍分类">
            <a-select
              v-model="params.first_category_id"
              :options="firstCateOptions"
              placeholder="一级分类"
              allowClear
            ></a-select>
          </a-form-model-item>

          <a-form-model-item>
            <a-select
              v-model="params.second_category_id"
              :options="secondCateOptions"
              placeholder="二级分类"
              allowClear
            ></a-select>
          </a-form-model-item>

          <a-form-model-item label="书籍状态">
            <a-select
              v-model="params.book_status"
              :options="book_status"
              allowClear
            ></a-select>
          </a-form-model-item>  
        </a-form-model-item>

        <a-form-model-item>
          <a-button type="primary" style="margin-right: 20px;" @click="createBook">创建书籍</a-button>
          <a-button type="primary" @click="openImport">批量导入</a-button>
        </a-form-model-item>

      </a-form-model>
    </div>
    
    <s-table
      ref="table"
      bordered
      size="default"
      row-key="id"
      :columns="columns"
      :data="loadData"
    >
      <template slot="first_cate" slot-scope="text">
        {{ text.firstCate && text.firstCate.category_name }}
      </template>
      <template slot="second_cate" slot-scope="text">
        {{ text.secondCate && text.secondCate.category_name }}
      </template>
      <template slot="status" slot-scope="text">
        {{ text.status === 1 ? '上架' : '下架' }}
      </template>
      <template slot="action" slot-scope="text">
        <a-button type="link" @click="viewBookDetail(text.id)">查看</a-button>
        <a-button type="link" @click="editBookDetail(text.id)">编辑</a-button>
        <a-button type="link" @click="offBook(text)">{{ text.status === 1 ? '下架' : '上架' }}</a-button>
        <a-button type="link" @click="deleteBook(text.id)">删除</a-button>
      </template>
    </s-table>

    <!-- 查看书籍详情 -->
    <a-modal
      v-model="bookDetailVisible"
      :footer="null"
      title="书籍详情"
      width="50%"
    >
      <h2>基本信息</h2>
      <a-row
        type="flex"
        class="book_info"
      >
        <a-col
          :md="12"
          :lg="8"
        >
          <span>书籍名称：</span>
          <span>{{ bookDetail.book_name }}</span>
        </a-col>
        <a-col
          :md="12"
          :lg="8"
        >
          <span>书籍作者：</span>
          <span>{{ bookDetail.author }}</span>
        </a-col>
        <a-col
          :md="12"
          :lg="8"
        >
          <span>一级分类：</span>
          <span>{{ bookDetail.firstCate ? bookDetail.firstCate.category_name : "" }}</span>
        </a-col>
        <a-col
          :md="12"
          :lg="8"
        >
          <span>二级分类：</span>
          <span>{{ bookDetail.secondCate ? bookDetail.secondCate.category_name : "" }}</span>
        </a-col>
        <a-col
          :md="12"
          :lg="8"
        >
          <span>馆藏量：</span>
          <span>{{ bookDetail.stock + '/' + bookDetail.total }}</span>
        </a-col>
        <a-col
          :md="12"
          :lg="8"
        >
          <span>书籍编码：</span>
          <span>{{ bookDetail.book_code }}</span>
        </a-col>
        <a-col
          :md="12"
          :lg="8"
        >
          <span>书籍位置：</span>
          <span>{{ bookDetail.place }}</span>
        </a-col>
        <a-col
          :md="12"
          :lg="8"
        >
          <span>书籍状态：</span>
          <span>{{ bookDetail.status == 1 ? '正常' : '下架' }}</span>
        </a-col>
      </a-row>
      <h2>借阅记录</h2>
      <div>
        <a-row>
          <a-col :span="6">时间</a-col>
          <a-col :span="6">用户</a-col>
          <a-col :span="6">操作</a-col>
          <a-col :span="6">状态</a-col>
        </a-row>
        <a-row>
          <a-col :span="6">2021-1-5 21:47</a-col>
          <a-col :span="6">admin</a-col>
          <a-col :span="6">归还</a-col>
          <a-col :span="6">正常</a-col>
        </a-row>
      </div>
    </a-modal>

    <a-modal
      v-model="importVisible"
      title="导入分类"
      width='60%'
      :footer="null"
    >
      <a-button type="primary" style="margin-bottom: 12px;">
        <a :href="proxy + prefix + '/book/template'" download>模板下载</a>
      </a-button>
      <a-form
        layout="inline"
      >
        <a-form-item label="请选择文件上传">
          <a-upload :file-list="fileList" :remove="handleRemove" :before-upload="beforeUpload">
            <a-button v-if="fileList.length === 0"> <a-icon type="upload" /> 选择文件 </a-button>
          </a-upload>
        </a-form-item>
        <a-form-item>
          <a-button type="primary" @click="uploadFile">上传</a-button>
        </a-form-item>
      </a-form>
      <h3 style="padding: 12px 0;">错误信息</h3>
      <div>导入成功{{bookResult.successNum || 0}}条数据，导入失败{{bookResult.errorNum || 0}}条数据，请修改后再次导入！</div>
      <s-table
        ref="resultTable"
        bordered
        size="default"
        :row-key="record => record.errMsg + Math.random()"
        :columns="resultColumn"
        :data="resultData"
      ></s-table>
    </a-modal>

  </div>
</template>
<script>
import { Breadcrumb, FormModel, Select, Modal } from 'ant-design-vue'
import STable from '@/components/Table'
import { mapState, mapActions } from 'vuex'
export default {
  name: 'BookManage',
  components: { 
    ABreadcrumb: Breadcrumb, 
    ABreadcrumbItem: Breadcrumb.Item, 
    AFormModel: FormModel, 
    AFormModelItem: FormModel.Item,
    ASelect: Select,
    STable,
    AModal: Modal
  },
  data() {
    let book_status = [
      {
        label: '全部',
        value: 0
      },
      {
        label: '上架',
        value: 1
      },
      {
        label: '下架',
        value: 2
      }
    ]

    let params = {
      keyword: '',
      first_category_id: undefined,
      second_category_id: undefined,
      book_status: 0
    }

    let columns = [
      {
        title: 'id',
        dataIndex: 'id',
      },
      {
        title: '书籍名称',
        dataIndex: 'book_name',
      },
      {
        title: '作者',
        dataIndex: 'author',
      },
      {
        title: '一级分类',
        scopedSlots: { customRender: "first_cate"},
      },
      {
        title: '二级分类',
        scopedSlots: { customRender: "second_cate"},
      },
      {
        title: '馆藏量',
        dataIndex: 'stock',
      },
      {
        title: '书籍编号',
        dataIndex: 'book_code',
      },
      {
        title: '书籍位置',
        dataIndex: 'place',
      },
      {
        title: '状态',
        scopedSlots: { customRender: "status"},
      },
      {
        title: '操作',
        width: '250px',
        scopedSlots: { customRender: "action"},
      },
    ]    

    const resultColumn = [
      {
        title: '书籍名称',
        dataIndex: 'book_name',
      },
      {
        title: '作者',
        dataIndex: 'author',
      },
      {
        title: '一级分类',
        dataIndex: 'firstCate',
      },
      {
        title: '二级分类',
        dataIndex: 'SecondCate',
      },
      {
        title: '馆藏量',
        dataIndex: 'stock',
      },
      {
        title: '书籍编号',
        dataIndex: 'code',
      },
      {
        title: '书籍位置',
        dataIndex: 'place',
      },
      {
        title: '书籍简介',
        dataIndex: 'introduction'
      },
      {
        title: '报错信息',
        dataIndex: 'errMsg'
      }

    ]

    return {
      book_status,
      params,
      columns,
      resultColumn,
      bookDetailVisible: false,
      importVisible: false,
      fileList: [],
      proxy: process.env.VUE_APP_API_URL,
      prefix: process.env.VUE_APP_PREFIX,
      loadData: paramters => {
        return this.getBooksList(Object.assign({}, this.params, paramters))
      },
      resultData: paramters => {
        return new Promise((resolve) => {
          resolve({
            list: this.bookResult.errorData || [],
            total: this.bookResult.errNum || 0
          })
        })
      }
    }
  },
  computed: {
    ...mapState('booksManage', ['allCategoryList', 'bookDetail']),
    ...mapState('file', ['bookResult']),
    secondCateOptions() {
      return this.allCategoryList && this.allCategoryList.second && this.allCategoryList.second.map(item => {
          return {
            label: item.category_name,
            value: item.id
          }
        })
    },
    firstCateOptions() {
      return this.allCategoryList && this.allCategoryList.first && this.allCategoryList.first.map(item => {
          return {
            label: item.category_name,
            value: item.id,
          }
        })
    }
  },
  watch: {
    importVisible() {
      if (!this.importVisible) {
        console.log('a')
        this.$store.commit('file/SET_BOOK_RESULT', {})
        this.$refs.resultTable.refresh(true)
        this.fileList = []
      }
    }
  },
  methods: {
    ...mapActions('booksManage', {
      getBooksList: 'getBooks',
      delBook: 'deleteBook',
      getBookDetail: 'getBookDetail',
      _offbook: 'offbook'
    }),
    ...mapActions('file', {
      bookTemplate: 'bookTemplate',
      bookImport: 'bookImport'
    }),
    viewBookDetail(id) {
      this.getBookDetail(id).then(res => {
        this.bookDetailVisible = true
      })
      
    },
    editBookDetail(id) {
      this.$router.push({
        name: 'Book Detail',
        params: {
          id
        }
      })
    },
    offBook(text) {
      this._offbook({
        id: text.id,
        data: {
          status: text.status === 1 ? 2 : 1
        }
      }).then(res => {
        this.$message.success('操作成功')
        this.$refs.table.refresh(true)
      })
    },
    deleteBook(id) {
      const _this = this
      this.$confirm({
        title: '删除书籍',
        content: '你确定要删除些书籍？',
        okText: '确认',
        cancelText: '取消',
        onOk() {
          _this.delBook(id).then(res => {
            _this.$message.success('删除成功')
            _this.$refs.table.refresh(true)
          })
        },
      })
    },
    search() {
      this.$refs.table.refresh(true)
    },
    reset() {
      this.params = {
        keyword: '',
        first_category_id: undefined,
        second_category_id: undefined,
        book_status: 0
      }
      this.$refs.table.refresh(true)
    },
    createBook() {
      this.$router.push({
        name: 'Book Create'
      })
    },
    search() {
      this.$refs.table.refresh(true)
    },
    handleRemove(file) {
      const index = this.fileList.indexOf(file);
      const newFileList = this.fileList.slice();
      newFileList.splice(index, 1);
      this.fileList = newFileList;
    },
    beforeUpload(file) {
      this.fileList = [...this.fileList, file];
      return false;
    },
    getTemplate() {
      this.bookTemplate().then(res => {
        this.download(res)
      })
      
    },
    uploadFile() {
      if (this.fileList.length === 0) {
        this.$message.info('请先上传模板文件')
        return ;
      }
      let formdata = new FormData()
      formdata.append('file', this.fileList[0])
      this.bookImport(formdata).then(res => {
        this.$message.info(res.message)
        this.$refs.resultTable.refresh(true)
        this.$refs.table.refresh(true)
      })
    },
    openImport() {
      this.importVisible = true
    }
  }
}
</script>
<style lang="scss" scoped>
.manage {
  .ant-select {
    width: 174px;
  }
  .ant-table {
    .ant-btn {
      padding: 0 8px;
    }
  }
  .ant-form {
    >.ant-form-item {
      margin-bottom: 5px;
    }
  }
}
.book_info {
  .ant-col {
    margin-bottom: 15px;
  }
}
/deep/.ant-upload {
  width: 300px;
  button {
    width: 100%;
  }
}
/deep/.ant-upload-list-item {
  width: 300px;
}
</style>
